const prodCat = require("../data/product-category.json");
const newProd = require("../data/new-product.json");

const prodCatHtml = window.document.querySelector("#product-category");
prodCat.forEach(prod => {
  prodCatHtml.innerHTML += `
          <div class="product-card-item">
            <div class="card-image">
              <img src="${prod.imageUrl}" alt="${prod.alt}">
            </div>
            <div class="card-info">
              <a href="#">${prod.name}</a>
            </div>
          </div>
  `;
});
const newProdHtml = window.document.querySelector("#new-product");
newProd.forEach(prod => {
  newProdHtml.innerHTML += `
          <div class="product-card-item">
            <div class="card-wrapper ${prod.cssClass}">
              <div class="card-image-full">
                <img src="${prod.imageUrl}" alt="${prod.alt}">
              </div>
              <div class="card-info-top">
                <div class="card-content-header-upper">
                  <h3>${prod.name}</h3>
                </div>
                <div class="card-content-header-lower">
                  <a href="#">${prod.desc}</a>
                </div>
                <div class="card-content-desc">From <span>${prod.unitPrice}</span> or <span>${prod.monthlyPay}</span>/mo. for ${prod.totalMonth} mon.</div>
              </div>
            </div>
          </div>
  `;
});

